<template>
        <div class="page-container">
    <table>
        <div class="table-header">
        <input class="calc-input" type="text" placeholder="Type here..." v-model="userInput" v-on:keydown.enter="getResult">
        <!-- <span class="operations">Operations</span> -->
        <span class="result" v-if="inputResult">{{inputResult}}</span>
        </div>
<tbody>
  <tr>
    <td @click="typePlus">+</td>
    <td @click="typeMinus">—</td>
    <td></td>
    <td class="clear" @click="deleteLastChar">Delete</td>
    <td class="clear" @click="clearInput">Clear input</td>
    <td class="clear" @click="clearAll">Clear all</td>
  </tr>
  <tr>
    <td @click="typeMultiply">x</td>
    <td @click="typeDivide">÷</td>
    <td></td>
    <td @click="typeOne">1</td>
    <td @click="typeTwo">2</td>
    <td @click="typeThree">3</td>
  </tr>
  <tr>
    <td></td>
    <td></td>
    <td></td>
    <td @click="typeFour">4</td>
    <td @click="typeFive">5</td>
    <td @click="typeSix">6</td>
  </tr>
  <tr>
    <td></td>
    <td></td>
    <td></td>
    <td @click="typeSeven">7</td>
    <td @click="typeEight">8</td>
    <td @click="typeNine">9</td>
  </tr>
  <tr>
    <td></td>
    <td></td>
    <td></td>
    <td @click="typeZero">0</td>
    <td @click="typeDot">.</td>
    <td @click="getResult">=</td>
  </tr>
</tbody>
</table>
</div>
</template>


<script>
export default {
    data () {
        return {
            userInput: [],
            inputResult:'',
            getResultCounter: 0,
        }
    },
    methods: {
        getResult () {
            let regExpEng = /[a-zA-Z]/g;
            let regExpRu = /[А-Яа-яЁё]+/;
            if (regExpEng.test(this.userInput) || regExpRu.test(this.userInput)) {
                alert ('Don\'t type letters, please')
                this.userInput = '';
            }
            else {
            this.inputResult = eval(this.userInput);
            }
        },
        clearInput () {
            this.userInput = '';
        },
        clearAll () {
            this.userInput = '';
            this.inputResult = '';
        },
        deleteLastChar () {
            this.userInput = this.userInput.slice(0,-1);
        },
        typePlus () {
            this.userInput = this.userInput + '+'
        },
        typeMinus () {
            this.userInput = this.userInput + '-'
        },
        typeMultiply () {
            this.userInput = this.userInput + '*';
        },
        typeDivide () {
            this.userInput = this.userInput + '/';
        },
        typeOne () {
            this.userInput = this.userInput + '1';
        },       
        typeTwo () {
            this.userInput = this.userInput + '2';
        },
        typeThree () {
            this.userInput = this.userInput + '3';
        },
        typeFour () {
            this.userInput = this.userInput + '4';
        },
        typeFive () {
            this.userInput = this.userInput + '5';
        },
        typeSix () {
            this.userInput = this.userInput + '6';
        },
        typeSeven () {
            this.userInput = this.userInput + '7';
        },
        typeEight () {
            this.userInput = this.userInput + '8';
        },
        typeNine () {
            this.userInput = this.userInput + '9';
        },
        typeZero () {
            this.userInput = this.userInput + '0';
        },
        typeDot () {
            this.userInput = this.userInput + '.';
        },
    }   

}
</script>


<style scoped>


.page-container {
    background-color: rgb(228, 228, 228);
    height:1200px;
    overflow-y:hidden;
    padding-top:35px;
    margin:0 auto;
    display:flex;
    justify-content: center;
}


input {
    background-color: rgb(19, 58, 90);
    color: rgb(218, 255, 252);
}


table {
background: rgb(0,61,96);
background: linear-gradient(45deg, rgba(0,61,96,0.5) 0%, rgba(0,203,143,0.5) 100%);
    font-family: 'Trebuchet MS';
    display:flex;
    flex-direction: column;
    width:400px;
    height:500px;
}

.result {
    color:rgb(27, 39, 46);
    font-size: 26px;
    padding-top:10px;
    padding-right:15px;
}

.table-header {
    text-align: end;
    display:flex;
    flex-direction: column ;
    height:200px;
}

.clear {
    font-size: 16px;
}

td {
    background-color: rgba(226, 226, 226, 0.5);
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    width:62px;
    height:62px;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    transition-duration: 0.1s;
}

td:hover {
    background-color: rgba(24, 72, 78, 0.1);
}

td:active {
    background-color: rgba(6, 25, 27, 0.2);
}


table, td{
    border:1px black solid;
}

.calc-input {
    font-family: 'Trebuchet MS';
    border:none;
    border-bottom: 1px solid black;
    border-radius: 0;
    text-align: end;
    font-size: 20px;
    height:50px;
}

::placeholder {
    padding-right:10px;
    font-size: 20px;
    color:rgb(186, 212, 226);
}

.calc-input:focus {
    outline: none;
}

.calc-input:focus::placeholder {
  color: transparent;
}

</style>